import React, { useEffect, useState, useRef } from "react";

import { Swiper, SwiperSlide } from "swiper/react";
import SwiperCore, { Autoplay } from "swiper";

import Button, { OutlineButton } from "../button/Button";
import Modal ,{ModalContent}from "../modal/Modal";

import pmdbApi, { category, movieType } from "../../api/pmdbApi";
import apiConfig from "../../api/apiConfig";

import { useNavigate } from "react-router-dom";

import "./hero-slide.scss";

const HeroSlide = () => {
  SwiperCore.use([Autoplay]);

  const [movieItems, setMovieItems] = useState([]);

  useEffect(() => {
    const getMovies = async () => {
      const params = { page: 1 };
      try {
        const response = await pmdbApi.getMoviesList(movieType.popular, {
          params,
        });
    
        setMovieItems(response.results.slice(1, 10));
      } catch {
        console.log("error");
      }
    };
    getMovies();
  }, []);

  return (
    <div className="hero-slide">
      <Swiper
        modules={[Autoplay]}
        grabCursor={true}
        spaceBetween={0}
        slidesPerView={1}
        // autoplay={{delay: 3000
      >
        {movieItems.map((item, i) => (
          <SwiperSlide key={i}>
            {({ isActive }) => (
              <HeroSlideItem
                item={item}
                className={`${isActive ? "active" : ""}`}
              />
            )}
          </SwiperSlide>
        ))}
      </Swiper>
      {
          movieItems.map((item, i) => (
              <TrailerModal key={i} item={item} />
          ))
      }
    </div>
  );
};

const HeroSlideItem = (props) => {
  let navigation = useNavigate();

  const item = props.item;

  const background = apiConfig.originalImage(
    item.backdrop_path ? item.backdrop_path : item.poster_path
  );

  const setModalActive = async () =>{
      const modal = document.querySelector(`#modal_${item.id}`);

      const videos = await pmdbApi.getVideos(category.movie, item.id);
      console.log(videos,11);

      if(videos.results.length > 0) {
          const videSrc= 'https://www.youtube.com/embed/' + videos.results[0].key;
          modal.querySelector('.modal__content > iframe').setAttribute('src', videSrc);

      }else{
          modal.querySelector('modal__content').innerHTML = 'No trailer';
      }
      modal.classList.toggle('active');
  }

  return (
    <div
      className={`hero-slide__item ${props.className} `}
      style={{ backgroundImage: `url(${background})` }}
    >
      <div className="hero-slide__item__content container">
        <div className="hero-slide__item__content__info">
          <h2 className="title">{item.title}</h2>
          <div className="overview">{item.overview}</div>
          <div className="btns">
            <Button onClick={() => navigation("movie/" + item.id)}>
              watch now
            </Button>
            <OutlineButton onClick={setModalActive}>
              watch trailer
            </OutlineButton>
          </div>
        </div>
        <div className="hero-slide__item__content__poster">
          <img src={apiConfig.w500Image(item.poster_path)} alt="" />
        </div>
      </div>
    </div>
  );
};

const TrailerModal = (props) => {
  const item = props.item;

  const iframeRef = useRef(null);

  const onClose = () => iframeRef.current.setAttribute('src', '');

 

  return (
    <Modal active={false} id={`modal_${item.id}`}>
    <ModalContent onClose={onClose}>
    <iframe ref={iframeRef} width="100%" height="500px" title="trailer" ></iframe>

    </ModalContent>
</Modal>
  );
};

export default HeroSlide;
